.Container {
  width: 100vw;
  height: 150vw; /* height:width ratio = 9/16 = .5625  */
  background: lightblue;
  max-height: 100vh;
  max-width: 67vh; /* 16/9 = 1.778 */
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0; /* vertical center */
  left: 0;
  right: 0; /* horizontal center */
  z-index: -9999;
}

.Connector1 {
  height: 7%;
  background: rgb(255, 13, 86);
  border-radius: 3vh;
  z-index: 100;
  width: 100%;
  box-shadow: inset 0vh -2vh 2vh 0vh rgba(0, 0, 0, 0.4);
  position: relative;
}
.Connector4 {
  height: 0.5%;
  width: 100%;
  background: lightblue;
  position: relative;
}
.Connector5 {
  height: 100%;
  width: 12%;
  margin-left: 80%;
  background: rgb(0, 0, 0, 0.4);
}
.Connector6 {
  height: 100%;
  width: 35%;
  margin-left: 10%;
  background: rgb(0, 0, 0, 0.4);
  margin-top: -0.8%;
}
