.Screen {
  border-radius: 3vh;
  background: rgb(255, 13, 86);
  margin: 0 auto;
  width: calc(100%-2vh);
  height: 45%;
  position: relative;
  box-shadow: inset 0vh 3vh 3vh 0vh rgb(255, 108, 152);
}

.Screen2 {
  background: rgb(25, 25, 25);
  padding: 2%;
  padding-bottom: 5%;
  margin-left: 6.5%;
  margin-top: 7%;
  width: 80%;
  height: 65%;
  position: absolute;
  border-top: 0.5vh rgb(167, 0, 50) solid;
  border-right: 0.5vh rgb(255, 135, 171) solid;
  border-left: 0.5vh rgb(167, 0, 50) solid;
  border-bottom: 0.5vh rgb(255, 135, 171) solid;
}

.Screen3 {
  border-radius: 10px;
  position: relative;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  background: green;
}

.Logo {
  display: block;
  width: 50%;
  margin: 0 auto;
}
