@font-face {
  font-family: myFirstFont;
  src: url(../../assets/fonts/joy.ttf);
}

.Container {
  font-family: myFirstFont;
  position: absolute;

  width: 100%;
  height: 100%;
  background: darkgrey;
}

.Link {
  font-size: 1.5vh;
  padding-left: 2vh;
  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
  display: block;
  line-height: 3vh;
  text-decoration: none;
  color: black;
  font-weight: 100;
}

.Link:hover {
  transition-duration: 3s;
  background: lightgrey;
}

.Selection {
  font-weight: 100;
  padding: 2vh;
  text-align: center;
  font-size: calc(2vh);
}
